.shopping-cart-page {
    display: flex;
    flex-direction: column;

    &-breadcrumbs {
        width: 100%;
        display: flex;
        flex-direction: row;
        column-gap: 4px;
        padding-left: 20px;
        box-shadow: 0 11px 8px 0 #909090;

        a {
            text-decoration: none;
        }
        div {
            cursor: default;
        }
    }

    &-scroll-container {
        height: calc(100vh - 88px);
        width: 100%;
        overflow-y: scroll;
        padding-top: 20px;
    }

    &-container {
        max-width: 1320px;
        width: 100%;
        padding: 0 20px;
    }

    &-title {
        font-size: 32px;
        font-weight: 600;
        line-height: 1.2;
        margin-bottom: 12px;
    }

    &-orders {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &-mainInfo {
        display: flex;
        flex-direction: row;
        column-gap: 8px;
        margin-bottom: 8px;
    }

    &-img {
        width: 120px;
        object-fit: cover;
        max-height: 126px;
    }

    &-info {
        display: flex;
        flex-direction: column;
        row-gap: 4px;
        width: 100%;

        &-row {
            display: flex;
            flex-direction: row;
            gap: 3px;
        }
        &-title {
            font-size: 16px;
            line-height: 1.375;
            font-weight: 600;
        }

        &-description {
            font-size: 16px;
            line-height: 1.375;
            font-weight: 400;
        }
    }

    &-buttons {
        display: flex;
        flex-direction: row;
        column-gap: 10px;
    }

    &-button {
        border-radius: 4px;
        border: 0;
        font-size: 16px;
        font-weight: 400;
        line-height: 1.625;
        padding: 0 14px;
        cursor: pointer;
        border: 1px solid #ff8a8a;

        &_action {
            background-color: #ff8a8a;
            &:hover {
                background-color: #ffa5a5;
            }
        }
    }
}
